import React from "react";
import {getInstanceMarketplace, getInstanceAvaxSki, getWeb3,contractAddressMarketplace}  from "../Web3Util";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Web3 from 'web3';

import skiteamlogo from '../images/skiteamlogo.png'; 

import SkierWalletComponent from '../components/SkierWalletComponent';
import HeaderComponent from '../components/HeaderComponent';


const MORE_PER_PAGE = 6

class WalletPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isClaimingRewardsMinting:false,
      myRewardsMinting:-1,
      userTokens:[],
      page:1,
      isLoadingMintingRewards:true,
      listedTokens:[],
      approvedTokens:[],
      finishedLoading:false,
      currentCounter:MORE_PER_PAGE
    };
    this.claimRewards = this.claimRewards.bind(this)
    this.getSkiers = this.getSkiers.bind(this)
    this.showMore = this.showMore.bind(this)

  }
  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstanceAvaxSki = await getInstanceAvaxSki(window.web3);
      const contractInstanceMarketplace = await getInstanceMarketplace(window.web3);
      
      this.setState({ user: window.user });
      this.setState({ contractInstanceAvaxSki,contractInstanceMarketplace});
      await this.loadData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
      })
    }
  }

  async loadData() {
    if(window.user == undefined){
      this.setState({
        isLoadingMintingRewards:false
      })
      return
    }
    try {
      let userTokens = []
      let myRewardsMinting = 0

      let toAddr = window.user;
      let incomingTokenTransferEvents = await this.state.contractInstanceAvaxSki.getPastEvents('Transfer', { filter: {'to': toAddr}, fromBlock: 0, toBlock: 'latest'})
      let outgoingTokenTransferEvents = await this.state.contractInstanceAvaxSki.getPastEvents('Transfer', { filter: {'from': toAddr}, fromBlock: 0, toBlock: 'latest'})

      incomingTokenTransferEvents.forEach( (event) =>{
        userTokens.push(event.returnValues.tokenId)
      });
     
      outgoingTokenTransferEvents.forEach( (event) =>{
        const index = userTokens.indexOf(event.returnValues.tokenId);
        if (index > -1) {
          userTokens.splice(index, 1);
        }
      });

      var collator = new Intl.Collator(undefined, {numeric: true, sensitivity: 'base'});

      userTokens.sort(collator.compare)

      this.setState({
        userTokens
      })

      for(let counter=0;counter<userTokens.length;++counter){
        let tokenID = userTokens[counter]
        let tokenRewards = await this.state.contractInstanceAvaxSki.methods.getReflectionBalance(tokenID).call()
        myRewardsMinting+=Number.parseInt(tokenRewards)
      }

      this.setState({
        myRewardsMinting,
        isLoadingMintingRewards:false
      })

      // see tokens currently listed to find their price
      let listedTokens = []
      let cancelledTokens = []
      let soldTokens = []

      let incomingTokenListingEvents = await this.state.contractInstanceMarketplace.getPastEvents('ListingCreated', { filter: {}, fromBlock: 0, toBlock: 'latest'})
      let incomingTokenListingCancelledEvents = await this.state.contractInstanceMarketplace.getPastEvents('ListingCancelled', { filter: {}, fromBlock: 0, toBlock: 'latest'})
      let incomingTokenListingSaleEvents = await this.state.contractInstanceMarketplace.getPastEvents('Sale', { filter: {}, fromBlock: 0, toBlock: 'latest'})

      incomingTokenListingEvents.forEach( (event) =>{
        listedTokens.push(event.returnValues)
      });
     
      incomingTokenListingCancelledEvents.forEach( (event) =>{
        cancelledTokens.push(event.returnValues.tokenId)
      });

      incomingTokenListingSaleEvents.forEach( (event) =>{
        soldTokens.push(event.returnValues.tokenId)
      });

      let newListedTokens1 = []
      let newListedTokens2 = []

      for (var i = 0 ; i< listedTokens.length; i++) {
      
        let isTokenPresent = false
        for (var j =0; j < cancelledTokens.length; j++) {
          if(Number.parseInt(listedTokens[i].tokenId) == Number.parseInt(cancelledTokens[j])){
            isTokenPresent = true
            cancelledTokens.splice(j, 1);
            break;
          }
        }
        if(!isTokenPresent){
          newListedTokens1.push(listedTokens[i])
        }
      }
      for (var i = 0 ; i< newListedTokens1.length; i++) {
      
        let isTokenPresent = false
        for (var j =0; j < soldTokens.length; j++) {
          if(Number.parseInt(newListedTokens1[i].tokenId) == Number.parseInt(soldTokens[j])){
            isTokenPresent = true
            soldTokens.splice(j, 1);
            break;
          }
        }
        if(!isTokenPresent){
          newListedTokens2.push(newListedTokens1[i])
        }
      }
     
      this.setState({
        listedTokens:newListedTokens2,
        finishedLoading:true
      })

    } catch (e) {
      console.log('Error', e)
    }
  }

  async removeListing(skierID){
    this.setState({finishedLoading:false})

    try {
      await this.state.contractInstanceMarketplace.methods.cancelListing(skierID).send()
      await this.loadData()
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({finishedLoading:true})
    }
  }

  async listToken(skierID){
    var price = prompt("Enter skier price in AVAX");
    if(isNaN(price)){
      return
    }
    this.setState({finishedLoading:false})

    try {
      await this.state.contractInstanceMarketplace.methods.createListing(skierID,
        Web3.utils.toWei(price)
        ).send()
        await this.loadData()
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({finishedLoading:true})
    }
  }

  async approveToken(skierID){
    let approvedTokens = this.state.approvedTokens
    this.setState({finishedLoading:false})
    try {
      await this.state.contractInstanceAvaxSki.methods.approve(contractAddressMarketplace,skierID).send()
      approvedTokens.push(skierID)
      this.setState({
        approvedTokens
      })
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({finishedLoading:true})

    }
  }

  getSkiers(){
    let components = []
    let localCounter =0
    this.state.userTokens.forEach((userToken)=> {
        if(localCounter< this.state.currentCounter){
          let isApproved = false
          let skierDetails ={
            price:-1
          }
          this.state.listedTokens.forEach((listedToken)=>{
            if(userToken == Number.parseInt(listedToken.tokenId)){
              skierDetails = listedToken
            }
          })
          this.state.approvedTokens.forEach((approvedToken)=>{
            if(Number.parseInt(approvedToken) == Number.parseInt(userToken)){
              isApproved = true
            }
          })
          components.push(
            <Grid key={userToken} item md={4} xs={12} style={{textAlign:"center"}}>
              <SkierWalletComponent 
              isApproved={isApproved}
              callBackApprove={()=>this.approveToken(userToken)} 
              callBackList={()=>this.listToken(userToken)} 
              callBackRemove={()=>this.removeListing(userToken)} 
              price={skierDetails.price} 
              isMarketplace={false} 
              tokenID={userToken} 
              finishedLoading={this.state.finishedLoading}/>
            </Grid>
          )
         
        }
        localCounter++

    })
    return components
  }

  
  async claimRewards(){
    try {
        this.setState({isLoadingMintingRewards:true})

        await this.state.contractInstanceAvaxSki.methods.claimRewards().send()
        await this.loadData()
      } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({isLoadingMintingRewards:false})
    }
  }

  showMore(){
    let currentCounter = this.state.currentCounter
    currentCounter+=MORE_PER_PAGE
    this.setState({
      currentCounter
    })
  }
  
  render() {
    return(
       <>
       <Container>
          <HeaderComponent/>

          </Container>
          <Container style={{margin:"25px"}}>
            <Grid container spacing={2} >
              <Grid item md={6} xs={6} style={{textAlign:"center"}}>
                <h1 className="my-skiers-title">My Skiers</h1>
                <h2 className="minted-skiers">View your ski team!</h2>
              </Grid>
              <Grid item md={6} xs={6} style={{textAlign:"center"}}>
                {this.state.isLoadingMintingRewards?
                  <a className="loading-animation" onClick={()=>this.claimRewards()}>Loading minting rewards... </a>
                :
                  <>
                    {this.state.myRewardsMinting>=0?
                      <a onClick={()=>this.claimRewards()}>Claim minting rewards: {Number(Web3.utils.fromWei(this.state.myRewardsMinting+"","ether")).toFixed(4)} AVAX </a>
                    : null}
                  </>
                }
              </Grid>
            </Grid>
            <Grid container spacing={4}>
            
              {this.getSkiers()}
            </Grid>

            {this.state.currentCounter< this.state.userTokens.length ? 
              <>
              <Grid item md={12} xs={12} style={{textAlign:"center"}}>
                <br />
                  <a onClick={()=>this.showMore()} style={{textAlign:"center"}}>Click here to show more</a>
              </Grid>
              </>
            : null}
           
          </Container>
         
          <div style={{textAlign:"center",paddingTop:"100px"}}>
            <img className="floating-logo" style={{height:"75px"}} src={skiteamlogo} />
          </div>
       </>
    )
  }
}

export default WalletPage;
