import Web3 from 'web3';
import AvaxSkiABI from './abi/avaxski.json'
import MarketplaceABI from './abi/marketplace.json'

// testnet 
// const contractAddressAvaxSki = "0xA8FAF88DD137e7af6703f13ce7c9191e458D2cEe"
// export const contractAddressMarketplace = "0x10c4Ffac048AB2c4e04c26abF06632a483a865c7"

// mainnet 
const contractAddressAvaxSki = "0x2fCb6A640cFCfEcE15D9582660Fa7294150DA93e"
export const contractAddressMarketplace = "0x1323Da945912F1DcC5c301Bf1542A622Fd015553"

export const getWeb3 = () =>
    new Promise(async(resolve, reject) => {
    if (window.ethereum) {
        const web3 = new Web3(window.ethereum);
        try {
            await window.ethereum.enable();
        resolve(web3);
        } catch (error) {
        reject(error);
        }
    } else if (window.web3) {
        // load metamask provider
        const web3 = window.web3;
        console.log("Injected web3 detected.");
        resolve(web3);
    } else {
        // main net
        const provider = new Web3.providers.HttpProvider("https://api.avax.network/ext/bc/C/rpc");

        // test net 
        // const provider = new Web3.providers.HttpProvider("https://api.avax-test.network/ext/bc/C/rpc");

        const web3 = new Web3(provider);
        console.log("No web3 instance injected, using Local web3.");
        resolve(web3);
    }
});
export const getInstanceAvaxSki = async (web3) => {
    window.user = (await web3.eth.getAccounts())[0];
    
    window.instanceAvaxSki= new web3.eth.Contract(
        AvaxSkiABI,
        contractAddressAvaxSki,
        {
            from: window.user
        }
    );
    return window.instanceAvaxSki;
}

export const getInstanceMarketplace= async (web3) => {
    window.user = (await web3.eth.getAccounts())[0];
    
    window.instanceMarketplace= new web3.eth.Contract(
        MarketplaceABI,
        contractAddressMarketplace,
        {
            from: window.user
        }
    );
    return window.instanceMarketplace;
}


