import React from "react";
import {getInstanceMarketplace,getInstanceAvaxSki, getWeb3}  from "../Web3Util";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Web3 from 'web3';

import skiteamlogo from '../images/skiteamlogo.png'; 
import avaxlogo from '../images/avaxlogo.png'; 

import SkierWalletComponent from '../components/SkierWalletComponent';
import MarketFilterComponent from '../components/MarketFilterComponent';
import HeaderComponent from '../components/HeaderComponent';

import metadata from '../metadata/_metadata.json'; 

function compareAscendingID( a, b ) {
  if ( a.edition < b.edition ){
    return -1;
  }
  if ( a.edition > b.edition ){
    return 1;
  }
  return 0;
}

metadata.sort( compareAscendingID );
const MORE_PER_PAGE = 9

class MarketplacePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isClaimingRewardsMarket:false,
      myRewardsMarket:-1,
      totalMinted:0,
      shownMarketSkiers:[],
      page:1,
      totalVolume:-1,
      totalSales:-1,
      floorPrice:-1,
      finishedLoading:false,
      loadedRewardsMarket:false,
      sortValue:"NoSort",
      currentCounter:MORE_PER_PAGE
    };
    this.updateShownSkiers = this.updateShownSkiers.bind(this)
    this.claimRewards = this.claimRewards.bind(this)
    this.updateSort = this.updateSort.bind(this)
    
  }
  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstanceMarketplace = await getInstanceMarketplace(window.web3);
      const contractInstanceAvaxSki = await getInstanceAvaxSki(window.web3);
      
      this.setState({ user: window.user });
      this.setState({ contractInstanceMarketplace,contractInstanceAvaxSki});
      await this.loadData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      
    }
  }

  async loadData() {
    
    try {
   
      if(window.user != undefined){
        let myRewardsMarket =Number.parseInt(await this.state.contractInstanceMarketplace.methods.getReflectionBalances().call())
        this.setState({
          myRewardsMarket
        })
      }
      this.setState({
          loadedRewardsMarket:true
      })
      
      let totalMinted =Number.parseInt(await this.state.contractInstanceAvaxSki.methods.totalSupply().call())
      let marketSkiers =[]
      let shownMarketSkiers =[]

      for(let counter=0;counter<totalMinted;++counter){
        marketSkiers.push(metadata[counter])
      }
      shownMarketSkiers = marketSkiers

      this.setState({
        totalMinted,
        shownMarketSkiers,
        marketSkiers
      })

      let listedTokens = []
      let cancelledTokens = []

      let incomingTokenListingEvents = await this.state.contractInstanceMarketplace.getPastEvents('ListingCreated', { filter: {}, fromBlock: 0, toBlock: 'latest'})
      let incomingTokenListingCancelledEvents = await this.state.contractInstanceMarketplace.getPastEvents('ListingCancelled', { filter: {}, fromBlock: 0, toBlock: 'latest'})
      let incomingTokenListingSaleEvents = await this.state.contractInstanceMarketplace.getPastEvents('Sale', { filter: {}, fromBlock: 0, toBlock: 'latest'})
      
      incomingTokenListingEvents.forEach( (event) =>{
        listedTokens.push(event.returnValues)
      });

     
      incomingTokenListingCancelledEvents.forEach( (event) =>{
        cancelledTokens.push(event.returnValues.tokenId)
      });

      let newListedTokens1 = []
      let newListedTokens2 = []
      let soldTokens = []

      for (var i = 0 ; i< listedTokens.length; i++) {
      
        let isTokenPresent = false
        for (var j =0; j < cancelledTokens.length; j++) {
          if(Number.parseInt(listedTokens[i].tokenId) == Number.parseInt(cancelledTokens[j])){
            isTokenPresent = true
            cancelledTokens.splice(j, 1);
            break;
          }
        }
        if(!isTokenPresent){
          newListedTokens1.push(listedTokens[i])
        }
      }

      let totalVolume = 0
      incomingTokenListingSaleEvents.forEach( (event) =>{
        soldTokens.push(event.returnValues.tokenId)
        totalVolume+=Number.parseInt(event.returnValues.price)
      });

      for (var i = 0 ; i< newListedTokens1.length; i++) {
      
        let isTokenPresent = false
        for (var j =0; j < soldTokens.length; j++) {
          if(Number.parseInt(newListedTokens1[i].tokenId) == Number.parseInt(soldTokens[j])){
            isTokenPresent = true
            soldTokens.splice(j, 1);
            break;
          }
        }
        if(!isTokenPresent){
          newListedTokens2.push(newListedTokens1[i])
        }
      }
      
      let floorPrice = 0
      let counter =0
      newListedTokens2.forEach((listedToken)=>{
        if(counter==0){
          floorPrice = listedToken.price
          counter++
        }
        if(listedToken.price<floorPrice){
          floorPrice = listedToken.price
        }
      })

      this.setState({
        totalSales:incomingTokenListingSaleEvents.length,
        totalVolume,
        floorPrice
      })

      let newMarketSkiers = []
      marketSkiers.forEach((marketSkier)=>{
        let skierID = marketSkier.edition-1+""
        if(newListedTokens2.some((element)=>element.tokenId === skierID)){
          let localSkier = marketSkier
          let containsIsForSale = false
          localSkier.attributes.forEach((attribute)=>{
            if(attribute.trait_type == "Is for sale"){
              containsIsForSale = true
            }
          })
          if(!containsIsForSale){
            localSkier.attributes.push({
              "trait_type":"Is for sale",
              "value":"For sale"
            })
          }
          newListedTokens2.forEach((listedToken)=>{
            if(listedToken.tokenId ==skierID ){
              localSkier.price = listedToken.price
            } 
          })
          newMarketSkiers.push(localSkier)
        }else{
          let localSkier = marketSkier
          let containsIsForSale = false
          localSkier.attributes.forEach((attribute)=>{
            if(attribute.trait_type == "Is for sale"){
              containsIsForSale = true
            }
          })
          if(!containsIsForSale){
            localSkier.attributes.push({
              "trait_type":"Is for sale",
              "value":"Not for sale"
            })
          }
          localSkier.price = -1
          newMarketSkiers.push(localSkier)
        }
      })

      marketSkiers = newMarketSkiers
      shownMarketSkiers = marketSkiers
      this.setState({
        marketSkiers,
        shownMarketSkiers,
        finishedLoading:true
      })

    } catch (e) {
      console.log('Error', e)
    }
  }

   
  async claimRewards(){
    try {
        this.setState({isClaimingRewardsMarket:true,loadedRewardsMarket:false})

        await this.state.contractInstanceMarketplace.methods.claimRewards().send()
        await this.loadData()
      } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({isClaimingRewardsMarket:false,loadedRewardsMarket:true})
    }
  }

  async buySkier(skierID,price){
  
    try {

      await this.state.contractInstanceMarketplace.methods.buy(skierID).send({
        value:price
      })

    } catch (e) {
      console.log('Error', e)
  
    }finally{
      window.location.reload();
    }
  }

  getSkiers(){
    let components = []
    let localCounter =0

    this.state.shownMarketSkiers.forEach((marketSkier)=> {
        if(localCounter< this.state.currentCounter){
          components.push(
            <Grid key={marketSkier.edition} item md={4} xs={12} style={{textAlign:"center"}}>
              <SkierWalletComponent 
              callBackBuy={()=>this.buySkier(marketSkier.edition-1,marketSkier.price)} 
              tokenID={marketSkier.edition-1} 
              price={marketSkier.price} 
              isMarketplace={true}
              finishedLoading={this.state.finishedLoading}
              />
            </Grid>)
        }
        localCounter++
    })
    return components
  }

  showMore(){
    let currentCounter = this.state.currentCounter
    currentCounter+=MORE_PER_PAGE
    this.setState({
      currentCounter
    })
  }

  updateShownSkiers(newFilter){
    let localSkiers= this.state.marketSkiers
    let newShownSkiers = []
    localSkiers.forEach((skier)=>{

      let shouldShowSkier = true
      skier.attributes.forEach((attribute)=>{
        for (const [attributeName, selectedValue] of Object.entries(newFilter)) {

          if(selectedValue != 'disabled'){
            if(attribute.trait_type == attributeName &&
              attribute.value != selectedValue ){
                shouldShowSkier = false
            }
          }
        }
      })
      if(shouldShowSkier){

        newShownSkiers.push(skier)
      }
    })
   
    this.setState({
      shownMarketSkiers:newShownSkiers,
      sortValue:"NoSort"
    })
    
  }

  updateSort(event){
    
    let newArray = this.state.shownMarketSkiers
    if(event.target.value == "Ascending"){
      function compare( a, b ) {
        return  a.price - b.price 
      }
      newArray.sort( compare );
    }else if(event.target.value == "Descending"){
      function compare( a, b ) {
        return   b.price - a.price 
      }
      newArray.sort( compare );
    }
    this.setState({
      sortValue:event.target.value,
      shownMarketSkiers:newArray
    })
  }
  
  
  render() {

    return(
       <>
       <Container>
          <HeaderComponent />
          </Container>
          
          <Container style={{margin:"25px"}}>
            <Grid container spacing={2} >
              <Grid item md={6} xs={6} style={{textAlign:"center"}}>
                <h1 className="marketplace-title">Marketplace</h1>
                <h2 className="minted-skiers">Trade Skiers, claim rewards</h2>
              </Grid>
              <Grid item md={6} xs={6} style={{textAlign:"center"}}>
                {this.state.loadedRewardsMarket?
                  <>
                    {this.state.myRewardsMarket>=0?
                      <a onClick={()=>this.claimRewards()}>Claim market rewards: {Number(Web3.utils.fromWei(this.state.myRewardsMarket+"","ether")).toFixed(8)} AVAX </a>
                    :
                      null
                    }
                  </>
                :
                  <a className="loading-animation">Loading market rewards...</a>
                }
              </Grid>
            </Grid>

            <br />
            <Grid container spacing={2} >
              <Grid item md={4} xs={4} style={{textAlign:"center"}}>
                Floor price <br/>
                {this.state.floorPrice == -1 ?
                  <p className="loading-animation">Loading...</p>
                : 
                  <>
                    <img src={avaxlogo} style={{width:"20px",marginRight:"5px"}}/>{Number(Web3.utils.fromWei(this.state.floorPrice+"","ether")).toFixed(2)} 
                  </>
                }
              </Grid>
              <Grid item md={4} xs={4} style={{textAlign:"center"}}>
                Total volume <br/>
                {this.state.totalVolume== -1?
                  <p className="loading-animation">Loading...</p>
                  :
                  <><img src={avaxlogo}  style={{width:"20px",marginRight:"5px"}}/>{Number(Web3.utils.fromWei(this.state.totalVolume+"","ether")).toFixed(2)} </>
                }
              </Grid>
              <Grid item md={4} xs={4} style={{textAlign:"center"}}>
                Total sales  <br/>
                 {this.state.totalSales == -1?
                  <p className="loading-animation">Loading...</p>
                  :this.state.totalSales}
              </Grid>
            </Grid>
            <br />
            
            <Grid container spacing={4}>
              <Grid  item md={12} xs={12} style={{textAlign:"left"}}>
                <h3>Filters</h3>
              </Grid>
              <MarketFilterComponent allMarketSkiers={this.state.marketSkiers} callBack={this.updateShownSkiers}/>
            </Grid>
            {this.state.finishedLoading?
              <Grid container spacing={4}>
                <Grid  item md={12} xs={12} style={{textAlign:"left"}}>
                  <h3>Sort</h3>
                </Grid>
                <Grid  item md={12} xs={12} style={{textAlign:"left"}}>
                  <div style={{display:"table",float:"left",marginRight:"15px"}}>
                      <select onChange={(event)=>{this.updateSort(event)}} style={{float:"left",marginRight:"5px", width:"200px"}}>
                          <option  disabled selected={this.state.sortValue=="NoSort"} value="NoSort" >Sort by price</option>
                          <option  selected={this.state.sortValue=="Ascending"} value="Ascending" >Ascending</option>
                          <option  selected={this.state.sortValue=="Descending"} value="Descending" >Descending</option>
                      </select>
                  </div>
                </Grid>
              </Grid>
            :null}
            
            <Grid container spacing={4}>
              {this.getSkiers()}
            </Grid>

            {this.state.currentCounter< this.state.shownMarketSkiers.length ? 
              <>
              <Grid  item md={12} xs={12} style={{textAlign:"center"}}>
                <br />
                  <a onClick={()=>this.showMore()} style={{textAlign:"center"}}>Click here to show more</a>
              </Grid>
              </>
            : null}
          </Container>
         
          <div style={{textAlign:"center",paddingTop:"100px"}}>
            <img className="floating-logo" style={{height:"75px"}} src={skiteamlogo} />
          </div>
       </>
    )
  }
}

export default MarketplacePage;
