import React from "react";
import axios from "axios";
import PointsCalculator  from "./PointsCalculator";
import Grid from '@material-ui/core/Grid';
import Web3 from 'web3';

import beginner from "../images/beginnerlogo.png"
import intermediate from "../images/intermediatelogo.png"
import advanced from "../images/advancedlogo.png"
import pro from "../images/prologo.png"
import olympian from "../images/olympianlogo.png"
import avaxlogo from '../images/avaxlogo.png'; 

import metadata from '../metadata/_metadata.json'; 
function compareAscendingID( a, b ) {
    if ( a.edition < b.edition ){
      return -1;
    }
    if ( a.edition > b.edition ){
      return 1;
    }
    return 0;
  }
  
metadata.sort( compareAscendingID );

class SkierWalletComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingData: true,
      tokenMetadata:[],
      points:{}
    };

  }
  
  componentDidMount = async () => {
    try{
        // let result = await axios.get('https://cloudflare-ipfs.com/ipfs/Qmdji9gmTxDq6Xyy1bdYBjYcYkaLrPzpXkwd6JkVMCA8DW/'+this.props.tokenID)
        // this.setState({
        //     tokenMetadata:result.data
        // })
        let id = Number.parseInt(this.props.tokenID)
        // id+=1
        let result = metadata[id]
         this.setState({
            tokenMetadata:result
        })
        let points = PointsCalculator(result.attributes)
        this.setState({
            points
        })
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingData:false
      })
    }
  }


  getBodyPoints(points){
    let components = []
    if(points ==0){
        components.push(<span key={0} className="body-point-empty">&nbsp;</span>)
        components.push(<span key={1} className="body-point-empty">&nbsp;</span>)
        components.push(<span  key={2}className="body-point-empty">&nbsp;</span>)
        components.push(<span  key={3}className="body-point-empty">&nbsp;</span>)
    }else if(points ==1){
        components.push(<span key={0} className="body-point">&nbsp;</span>)
        components.push(<span key={1} className="body-point-empty">&nbsp;</span>)
        components.push(<span key={2} className="body-point-empty">&nbsp;</span>)
        components.push(<span key={3} className="body-point-empty">&nbsp;</span>)
    }
    else if(points ==2){
        components.push(<span key={0} className="body-point">&nbsp;</span>)
        components.push(<span key={1} className="body-point">&nbsp;</span>)
        components.push(<span key={2} className="body-point-empty">&nbsp;</span>)
        components.push(<span key={3} className="body-point-empty">&nbsp;</span>)
    }
    else if(points ==3){
        components.push(<span key={0} className="body-point">&nbsp;</span>)
        components.push(<span key={1} className="body-point">&nbsp;</span>)
        components.push(<span key={2} className="body-point">&nbsp;</span>)
        components.push(<span key={3} className="body-point-empty">&nbsp;</span>)
    }
    else if(points ==4){
        components.push(<span key={0} className="body-point">&nbsp;</span>)
        components.push(<span key={1} className="body-point">&nbsp;</span>)
        components.push(<span key={2} className="body-point">&nbsp;</span>)
        components.push(<span key={3} className="body-point">&nbsp;</span>)
    }
    return components
  }

  getHeadPoints(points){
    let components = []
    if(points ==0){
        components.push(<span  key={0}className="head-point-empty">&nbsp;</span>)
        components.push(<span  key={1}className="head-point-empty">&nbsp;</span>)
        components.push(<span  key={2}className="head-point-empty">&nbsp;</span>)
    }else if(points ==1){
        components.push(<span  key={0}className="head-point">&nbsp;</span>)
        components.push(<span key={1} className="head-point-empty">&nbsp;</span>)
        components.push(<span  key={2}className="head-point-empty">&nbsp;</span>)
    }
    else if(points ==2){
        components.push(<span  key={0}className="head-point">&nbsp;</span>)
        components.push(<span  key={1}className="head-point">&nbsp;</span>)
        components.push(<span  key={2}className="head-point-empty">&nbsp;</span>)
    }
    else if(points ==3){
        components.push(<span  key={0}className="head-point">&nbsp;</span>)
        components.push(<span  key={1}className="head-point">&nbsp;</span>)
        components.push(<span  key={2}className="head-point">&nbsp;</span>)
    }
    return components
  }

  getFacePoints(points){
    let components = []
    if(points ==0){
        components.push(<span key={0} className="face-point-empty">&nbsp;</span>)
        components.push(<span key={1} className="face-point-empty">&nbsp;</span>)
    }else if(points ==1){
        components.push(<span  key={0}className="face-point">&nbsp;</span>)
        components.push(<span  key={1}className="face-point-empty">&nbsp;</span>)
    }
    else if(points ==2){
        components.push(<span  key={0}className="face-point">&nbsp;</span>)
        components.push(<span  key={1}className="face-point">&nbsp;</span>)
    }
   
    return components
  }

  getSkiPoints(points){
    let components = []
    if(points ==0){
        components.push(<span key={0} className="ski-point-empty">&nbsp;</span>)
        components.push(<span key={1} className="ski-point-empty">&nbsp;</span>)
        components.push(<span key={2} className="ski-point-empty">&nbsp;</span>)
    }else if(points ==1){
        components.push(<span key={0} className="ski-point">&nbsp;</span>)
        components.push(<span key={1} className="ski-point-empty">&nbsp;</span>)
        components.push(<span key={2} className="ski-point-empty">&nbsp;</span>)
    }
    else if(points ==2){
        components.push(<span key={0} className="ski-point">&nbsp;</span>)
        components.push(<span key={1} className="ski-point">&nbsp;</span>)
        components.push(<span key={2} className="ski-point-empty">&nbsp;</span>)
    }
    else if(points ==3){
        components.push(<span key={0} className="ski-point">&nbsp;</span>)
        components.push(<span key={1} className="ski-point">&nbsp;</span>)
        components.push(<span key={2} className="ski-point">&nbsp;</span>)
    }
    return components
  }

  getGlovePoints(points){
    let components = []
    if(points ==0){
        components.push(<span  key={0}className="glove-point-empty">&nbsp;</span>)
        components.push(<span  key={1}className="glove-point-empty">&nbsp;</span>)
    }else if(points ==1){
        components.push(<span  key={0}className="glove-point">&nbsp;</span>)
        components.push(<span  key={1}className="glove-point-empty">&nbsp;</span>)
    }
    else if(points ==2){
        components.push(<span  key={0}className="glove-point">&nbsp;</span>)
        components.push(<span  key={1}className="glove-point">&nbsp;</span>)
    }
   
    return components
  }
  render() {
      let id = Number.parseInt(this.props.tokenID)
      id+=1
    if(this.state.points.tier == undefined){
     <Grid container>
        <Grid item md={12} xs={12} >
            <div>
               Loading
            </div>
        </Grid>
    </Grid>
    }
      /*
        Expression 
        Body 4
        Gloves 2
        Skis 3
        Head 3
        Face 2
        */
       let logoImage = beginner
       if(this.state.points.tier == "Intermediate"){
            logoImage = intermediate
       } else if(this.state.points.tier == "Advanced"){
            logoImage = advanced
       }    
        else if(this.state.points.tier == "Pro"){
            logoImage = pro
        }
        else if(this.state.points.tier == "Olympian"){
            logoImage = olympian
        }
    return(
        <div style={{margin:"auto",border:"2px solid #212529", borderRadius:"10px",overflow:"hidden"}}>
            <Grid container>
                <Grid item md={12} xs={12} style={{margin:"auto"}}>
                    <img style={{width:"100%",borderBottom:"2px solid #212529"}} src={this.state.tokenMetadata.image} />
                </Grid>
            </Grid>
            <br />
          
           {this.state.points.expression ==""
            ?
            <>
            <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Skier:&nbsp;#{id}</p>
                <img style={{width:"auto",height:"110px",float:"right",margin:"-50px",marginRight:"-35px"}} src={olympian} />
            </div>
            <br /><br />
            <div style={{paddingLeft:"20px",paddingRight:"20px"}}>
                <p style={{float:"left"}}>Expression</p>
                <p style={{float:"right"}}>Unique</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Body</p>
                <p className="body-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getBodyPoints(4)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Head</p>
                <p className="head-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getHeadPoints(3)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Face</p>
                <p className="glove-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getFacePoints(2)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Skis</p>
                <p className="ski-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getSkiPoints(3)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Gloves</p>
                <p className="glove-points-container" style={{float:"right",width:"70%",display:"table"}}>{this.getGlovePoints(2)}</p>  
            </div>      
            </>
            :
            <>
            <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Skier:&nbsp;#{id}</p>
                <img style={{width:"auto",height:"110px",float:"right",margin:"-50px",marginRight:"-35px"}} src={logoImage} />
            </div>
            <br /><br />
            <div style={{paddingLeft:"20px",paddingRight:"20px"}}>
                <p style={{float:"left"}}>Expression</p>
                <p style={{float:"right"}}>{this.state.points.expression}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Body</p>
                <p className="body-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getBodyPoints(this.state.points.bodyPoints)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Head</p>
                <p className="head-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getHeadPoints(this.state.points.headsPoints)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Face</p>
                <p className="glove-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getFacePoints(this.state.points.facePoints)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Skis</p>
                <p className="ski-points-container" style={{float:"right",width:"80%",display:"table"}}>{this.getSkiPoints(this.state.points.skisPoints)}</p>  
            </div>
            <br /><br />
           <div style={{paddingLeft:"20px",paddingRight:"10px"}}>
                <p style={{float:"left"}}>Gloves</p>
                <p className="glove-points-container" style={{float:"right",width:"70%",display:"table"}}>{this.getGlovePoints(this.state.points.glovesPoints)}</p>  
            </div>      
            </>
            }

            {this.props.price != undefined?
                <>
                {this.props.price >0?
                <>
                <br /><br />
                <div style={{paddingLeft:"20px",paddingRight:"25px"}}>
                    {this.props.isMarketplace?
                        <p style={{float:"left"}}><a onClick={this.props.callBackBuy}>Buy</a></p>
                        :
                        <p style={{float:"left"}} > <a onClick={this.props.callBackRemove}>Remove listing</a></p>
                    }
                    <p style={{float:"right"}}><img src={avaxlogo} style={{width:"20px",marginRight:"5px"}}/>{ Number(Web3.utils.fromWei(this.props.price+"","ether")).toFixed(2)}  </p>
                </div>   
                </>
                : 
                <>
                    <br /><br />
                    <div style={{paddingLeft:"20px",paddingRight:"20px"}}>
                        {this.props.isMarketplace?
                            
                            <p style={{float:"left"}}>Skier not for sale</p>
                        :
                            <>
                                {this.props.finishedLoading?
                                <>
                                    {this.props.isApproved?
                                    <p style={{float:"left"}}> <a onClick={this.props.callBackList}>Set skier price</a></p>
                                :
                                    <p style={{float:"left"}}> <a onClick={this.props.callBackApprove}>Approve token</a></p>
                                }
                                </>
                                :
                                <p style={{float:"left"}} className="loading-animation"> Loading...</p>

                                }
                                
                            </>
                        }
                    </div>   
                </>
                }
                </>
                : 
                <>
                {this.props.isMarketplace?
                     <>
                     <br /><br />
                    <div style={{paddingLeft:"20px",paddingRight:"25px"}}>
                            <p style={{float:"left"}} className="loading-animation">Loading...</p>
                    </div>
                    </>
                    : null
                    }
                </>
            }
           
         
            <br />
        </div>
    )
  }
}

export default SkierWalletComponent;
