import React from "react";
import axios from "axios";
import PointsCalculator  from "./PointsCalculator";
import Grid from '@material-ui/core/Grid';

class MarketFilterComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingData: true,
      tokenMetadata:[],
      attributes:{},
      selectedFilters:{}
    };
    this.resetFilter = this.resetFilter.bind(this)
    this.updateSelectedFilter = this.updateSelectedFilter.bind(this)
  }

    componentDidUpdate(prevProps){
        if(prevProps.allMarketSkiers == this.props.allMarketSkiers){
            return 
        }
        let attributes = {}
        let selectedFilters = {}
        // set attribute types
        this.props.allMarketSkiers.forEach((skier)=>{
            skier.attributes.forEach((attribute)=>{
                if(attributes[attribute.trait_type] == undefined){
                    attributes[attribute.trait_type]  = []
                    selectedFilters[attribute.trait_type] = 'disabled'
                }

                if(attributes[attribute.trait_type].indexOf(attribute.value) === -1) {
                    attributes[attribute.trait_type].push(attribute.value)
                }

                attributes[attribute.trait_type].sort()
               
            })
        })
    
        this.setState({
            attributes,
            selectedFilters
        })
    }

    resetFilter(attributeName){
        let selectedFilters = this.state.selectedFilters
        selectedFilters[attributeName] = 'disabled'
        this.setState({
            selectedFilters
        })
        this.props.callBack(selectedFilters)
    }

    updateSelectedFilter(event,attributeName){
        let selectedFilters = this.state.selectedFilters
        selectedFilters[attributeName] = event.target.value 
        this.setState({
            selectedFilters
        })
        this.props.callBack(selectedFilters)
    }

  getFilteringOptions(){
      let components = []
      for (const [attributeName, attributeValueList] of Object.entries(this.state.attributes)) {

        let attributeValues = []
        attributeValueList.forEach((value) =>{
            attributeValues.push(
                <option selected={this.state.selectedFilters[attributeName]==value} value={value}>
                    {value}
                </option>
            )
        })

        components.push(
            <div style={{display:"table",float:"left",marginRight:"15px"}}>
                <select onChange={(event)=>this.updateSelectedFilter(event,attributeName)} style={{float:"left",marginRight:"5px", width:"200px"}}>
                    <option  disabled selected={this.state.selectedFilters[attributeName]=='disabled'} value="empty" >{attributeName}</option>
                    {
                        attributeValues
                    }
                </select>
                <div  style={{float:"left"}} >
                    <a onClick={()=>this.resetFilter(attributeName)}>x </a>
                </div>
            </div>
        )
     
      }
      
    return(components)
  }

  render() {
   
    return(
        <Grid  item md={12} xs={12} style={{textAlign:"center",display:"inline-block"}}>
            {this.getFilteringOptions()}
        </Grid>
    )
  }
}

export default MarketFilterComponent;
